import React, { useEffect, useState, Fragment } from "react";
import Card from "@material-ui/core/Card";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import "./listOfAssessment.scss";
import * as AssessmentActions from "../../store/Assessment/AssessmentAction";
import TableComponent from "../../components/Controls/Table/sortableTable";
import SelectInput from "../../components/Atoms/SelectInput/SelectInput";
import CalenderCard from "../../components/Controls/calenderCard/calenderCard";
import DeleteIcon from "@material-ui/icons/Delete";
import CreateIcon from "@material-ui/icons/Create";
import ControlledDialog from "../../components/Atoms/Dialog";
import History from "../../routes/History";
import VisibilityIcon from "@material-ui/icons/Visibility";
import moment from "moment";
import { secondsToHours } from "../../utils/utility";
import ReactGA from "react-ga";

const ListOfAssessments = (props) => {
  let inboundDataColumn = [
    {
      Header: "Date",
      accessor: "date",
      Cell: (row) => {
        if (row.original) {
          return moment(row.original.createdAt).format("DD-MM-YYYY");
        }
      },
    },
    {
      Header: "Assessment Name",
      accessor: "name",
      minWidth: 100,
    },
    {
      Header: "Job Title",
      accessor: "jTitle",
      Cell: (row) => {
        if (row.original) {
          return row.original.jdId.title;
        }
      },
    },
    {
      Header: "Question Types",
      accessor: "type",
      Cell: (row) => {
        let data = [];
        if (row.original) {
          row.original.questionType.map((x) => {
            data.push(x.id.name);
          });
        }
        return data.toString();
      },
    },
    {
      Header: "Total Time(MM:SS)",
      accessor: "jTitle",
      Cell: (row) => {
        if (row.original && row.original.overAllTime) {
          return secondsToHours(row.original.overAllTime);
          // return secondsToHours(row.original.questionType.reduce((accum,item) => accum + item.totalTime, 0))
        } else {
          return secondsToHours(0);
        }
      },
    },
    {
      Header: "Skills",
      accessor: "skills",
      Cell: (row) => {
        if (row.original) {
          return row.original.jdId.skills.toString();
        }
      },
    },
    {
      Header: "",
      accessor: "",
      width: 150,
      Cell: (row) => {
        return (
          <div
            style={{
              display: "flex",
              justifyContent: "flex-start",
            }}
          >
            <div className='list-verbal-edit'>
              <VisibilityIcon
                onClick={() => handleViewTest(row.original)}
                style={{ height: "15px", width: "15px", color: "green" }}
              />
            </div>
            {row && row.original && row.original.isPublished ? null : (
              <div
                className='list-verbal-edit'
                onClick={() => handleEditTest(row.original)}
              >
                <CreateIcon
                  style={{ height: "15px", width: "15px", color: "#1473e6" }}
                />
              </div>
            )}
            {row && row.original && row.original.isPublished ? null : (
              <div className='list-verbal-edit'>
                <DeleteIcon
                  style={{ height: "15px", width: "15px", color: "red" }}
                  onClick={() => {
                    setOpenDailog(true);
                    setRowData(row.original);
                  }}
                />
              </div>
            )}
          </div>
        );
      },
    },
  ];
  const [tableData, setTableData] = useState([]);
  const [page, setPage] = useState(1);
  const [startDate, setStartDate] = useState(moment().subtract(30, "d"));
  const [endDate, setEndDate] = useState(moment().add(1, "d"));
  const [progress, setProgress] = useState(0);
  const [stop, setSTop] = useState(false);
  const [openDialog, setOpenDailog] = useState(false);
  const [rowData, setRowData] = useState({});
  const [limit, setPagelimit] = useState(10);
  const [jd, setJd] = useState({ label: "Select JD", value: "" });
  const { isGetAllTest, allTest, jobTypes, isTestDeleted } =
    props.AssessmentReducer;
  const allTestData = (allTest && allTest.data && allTest.data.results) || [];
  const totalPages = allTest && allTest.data && allTest.data.totalPages;
  const jobList = (jobTypes && jobTypes.data) || [];

  useEffect(() => {
    ReactGA.pageview(window.location.pathname);
    setSTop(false);
    setProgress(50);
    const payload = {
      page: page,
      limit: limit,
      startDate: startDate,
      endDate: endDate,
    };
    props.AssessmentAction.getAllTest(payload);
  }, []);

  useEffect(() => {
    props.AssessmentAction.getJobTypes();
  }, []);
  const handleNextPagination = (e) => {
    setPage(page + 1);
    const payload = {
      page: page + 1,
      limit: 10,
      startDate: startDate,
      endDate: endDate,
    };
    props.AssessmentAction.getAllTest(payload);
  };
  const handleBackPagination = (e) => {
    setPage(page - 1);
    const payload = {
      page: page - 1,
      limit: 10,
      startDate: startDate,
      endDate: endDate,
    };
    props.AssessmentAction.getAllTest(payload);
  };
  const handlePagination = (e) => {
    if (e) {
      setPage(e);
    }
  };
  const setLimit = (e) => {
    setPagelimit(e);
    const payload = {
      page: page,
      limit: e,
      startDate: startDate,
      endDate: endDate,
    };
    props.AssessmentAction.getAllTest(payload);
  };
  const handleSelect = (e) => {
    setJd(e);
    setProgress(60);
    setPage(1);
    const payload = {
      page: page,
      limit: limit,
      jdId: e.value,
      startDate: startDate,
      endDate: endDate,
    };
    props.AssessmentAction.getAllTest(payload);
  };
  useEffect(() => {
    if (isGetAllTest) {
      setProgress(100);
      setTableData(allTestData);
    }
  }, [isGetAllTest]);
  const deleteTest = () => {
    const payload = {
      id: rowData.id,
    };
    props.AssessmentAction.deleteTest(payload);
  };
  useEffect(() => {
    if (isTestDeleted) {
      const payload = {
        page: page,
        limit: limit,
        startDate: startDate,
        endDate: endDate,
      };
      props.AssessmentAction.getAllTest(payload);
      setOpenDailog(false);
    }
  }, [isTestDeleted]);
  const handleEditTest = (item) => {
    const id = item.id;
    History.push(`/assessment/step2/create-assignment/${id}`);
  };
  const handleViewTest = (item) => {
    const id = item.id;
    History.push({
      pathname: `/assessment/step2/publish/${id}`,
      state: { isFromList: true },
    });
  };
  return (
    <Fragment>
      <div className='list-resume-card'>
        <Card>
          <div className='list-assessment-wrapper'>
            <ControlledDialog
              showDialog={openDialog}
              handleCloseDialog={() => {
                setOpenDailog(false);
              }}
              dialogClassName={"deleteDialog"}
              dialogAction1Text='Cancel'
              dialogAction2Text='Confirm'
              showClose={true}
              iconTop={
                <DeleteIcon
                  style={{ height: "15px", width: "15px", color: "red" }}
                />
              }
              headingTop='Delete'
              iconTopClass='icon-top'
              headingTopClass='heading-top'
              dialogAction1Class='dialog-action-1-class'
              dialogAction2Class='dialog-action-2-class'
              handleAction2={deleteTest}
              handleAction1={() => {
                setOpenDailog(false);
              }}
            >
              <div className='dialog-main-heading'>
                Are you sure you want to delete this test?
              </div>
              <div className='dialog-sub-heading'>
                Click to confirm to delete this test permanently
              </div>
            </ControlledDialog>
            <div className='row' style={{ marginBottom: "20px" }}>
              <div style={{ width: "40%", marginTop: "8px" }}>
                <SelectInput
                  className='react-select'
                  onChange={handleSelect}
                  options={jobList.map((x) => ({
                    label: x.title,
                    value: x.id,
                  }))}
                  placeholder='Select JD'
                  value={jd}
                />
              </div>
              <div style={{ marginLeft: "20px", width: "25%" }}>
                <CalenderCard
                  handleSelectDate={(date) => {
                    setStartDate(date.selection.startDate);
                    setEndDate(date.selection.endDate);
                    const payload = {
                      page: page,
                      limit: 10,
                      startDate: date.selection.startDate,
                      endDate: date.selection.endDate,
                    };
                    props.AssessmentAction.getAllTest(payload);
                  }}
                  startDate={startDate}
                  endDate={endDate}
                />
              </div>
            </div>
            <TableComponent
              className={"jd-table"}
              data={tableData}
              columns={inboundDataColumn}
              sortable={true}
              pageSize={limit}
              setPageSize={(e) => setLimit(e)}
              handlePrev={handleBackPagination}
              handleNext={handleNextPagination}
              gotoPage={(e) => handlePagination(e)}
              pageNumber={page ? page : 1}
              totalPage={totalPages}
            />
          </div>
        </Card>
      </div>
    </Fragment>
  );
};
const mapDispatchToProps = (dispatch) => {
  return {
    AssessmentAction: bindActionCreators(AssessmentActions, dispatch),
  };
};
const mapStateToProps = (state) => {
  return {
    AssessmentReducer: state.AssessmentReducer,
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(ListOfAssessments);
